// src/api/userService/friends.ts
import { FRIENDSSERVICE_API } from "@/config/env";
import type { FriendsResDTO } from "@/types/userService/friends";

/* ============================================================
   1. ì¸ì¦ í† í° ê°€ì ¸ì˜¤ê¸° (NextJS SSR ë³´í˜¸)
============================================================ */
const getAuthToken = (): string | null => {
  if (typeof window === "undefined") return null;
  return localStorage.getItem("accessToken");
};

/* ============================================================
   2. ê³µí†µ í—¤ë” ìƒì„± (undefined ê°€ëŠ¥ì„± ì œê±°)
============================================================ */
const getAuthHeaders = (): Record<string, string> => {
  const token = getAuthToken();
  const headers: Record<string, string> = {};

  if (token) {
    headers["Authorization"] = `Bearer ${token}`;
  }

  return headers;
};

/* ============================================================
   3. ê³µí†µ fetch wrapper (credentials + íƒ€ì… ì•ˆì •ì„±)
============================================================ */
async function authorizedFetch(url: string, options: RequestInit = {}) {
  const headers: Record<string, string> = {
    ...(options.headers as Record<string, string> | undefined),
    ...getAuthHeaders(),
  };

  return fetch(url, {
    ...options,
    headers,
    credentials: "include", // â­ ë§¤ìš° ì¤‘ìš”
  });
}

/* ============================================================
   4. API functions
============================================================ */

// ì¹œêµ¬ ìƒíƒœ ì¡°íšŒ
export async function getFriendshipStatus(
    currentUserSignId: string,
    targetUserSignId: string
): Promise<FriendsResDTO | null> {
  const response = await authorizedFetch(
      `${FRIENDSSERVICE_API}/status?targetUserId=${encodeURIComponent(
          targetUserSignId
      )}`,
      { method: "GET" }
  );

  if (response.status === 204) return null;
  if (response.ok) return response.json();

  throw new Error(await response.text());
}

// ì¹œêµ¬ ìš”ì²­ ë³´ë‚´ê¸°
export async function sendFriendRequest(
    currentUserSignId: string,
    receiverSignId: string
): Promise<FriendsResDTO> {
  const response = await authorizedFetch(`${FRIENDSSERVICE_API}/request`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ receiverSignId }),
  });

  if (response.ok) return response.json();
  throw new Error(await response.text());
}

