// StartNewChatModal.tsx
"use client";

import React, { useState, useEffect } from 'react';
import { X, UserPlus, Loader2, Check } from 'lucide-react';
import { createChatRoom } from '@/api/chatService/chat';
import { getFriendList } from '@/api/userService/friends';
import styles from "@/styles/chatService/StartNewChatModal.module.css"; // ìƒˆ CSS ëª¨ë“ˆ

interface StartNewChatModalProps {
  currentUserId: string;
  onClose: () => void;
  onRoomCreated: (newRoom: { roomId: string; roomName: string }) => void;
}

export default function StartNewChatModal({ currentUserId, onClose, onRoomCreated }: StartNewChatModalProps) {
  const [friends, setFriends] = useState<any[]>([]);
  const [selectedFriends, setSelectedFriends] = useState<string[]>([]);
  const [roomName, setRoomName] = useState('');
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    const loadFriends = async () => {
      try {
        const data = await getFriendList();
        setFriends(data);
      } catch (err: any) {
        setError(err.message || "ì¹œêµ¬ ëª©ë¡ì„ ë¶ˆëŸ¬ì˜¤ì§€ ëª»í–ˆìŠµë‹ˆë‹¤.");
      } finally {
        setLoading(false);
      }
    };
    loadFriends();
  }, []);

  const toggleFriend = (userSignId: string) => {
    setSelectedFriends(prev =>
        prev.includes(userSignId)
            ? prev.filter(id => id !== userSignId)
            : [...prev, userSignId]
    );
  };

  const handleSubmit = async () => {
    if (selectedFriends.length === 0 || submitting) return;

    setSubmitting(true);
    setError('');

    try {
      const participantIds = [...selectedFriends, currentUserId];

      let defaultName = '';
      if (selectedFriends.length === 1) {
        const friend = friends.find(f => f.userSignId === selectedFriends[0]);
        defaultName = friend?.name || selectedFriends[0];
      } else {
        defaultName = `${selectedFriends.length + 1}ëª…ì˜ ê·¸ë£¹ ì±„íŒ…`;
      }

      const finalName = roomName.trim() || defaultName;

      const newRoom = await createChatRoom(participantIds, finalName);
      onRoomCreated({ roomId: newRoom.roomId, roomName: newRoom.roomName || finalName });
      onClose();
    } catch (err: any) {
      setError(err.message || "ì±„íŒ…ë°© ìƒì„± ì‹¤íŒ¨");
    } finally {
      setSubmitting(false);
    }
  };

  return (
      <>
        <div className={styles.backdrop} onClick={onClose} />
        <div className={styles.modal}>
          <div className={styles.header}>
            <h2>ìƒˆ ì±„íŒ… ì‹œì‘í•˜ê¸°</h2>
            <button onClick={onClose} className={styles.closeBtn}>Ã—</button>
          </div>

          <div className={styles.body}>
            {error && <div className={styles.error}>{error}</div>}

            <div className={styles.inputGroup}>
              <label>ì±„íŒ…ë°© ì´ë¦„ (ì„ íƒ)</label>
              <input
                  type="text"
                  value={roomName}
                  onChange={(e) => setRoomName(e.target.value)}
                  placeholder={selectedFriends.length === 1 ? "1:1 ì±„íŒ…" : "ê·¸ë£¹ ì±„íŒ… ì´ë¦„"}
              />
            </div>

            <div className={styles.friendListSection}>
              <div className={styles.sectionTitle}>
                ì¹œêµ¬ ì„ íƒ ({selectedFriends.length}ëª… ì„ íƒë¨)
              </div>

              {loading ? (
                  <div className={styles.loading}>
                    <Loader2 className="animate-spin" /> ì¹œêµ¬ ëª©ë¡ ë¡œë“œ ì¤‘...
                  </div>
              ) : friends.length === 0 ? (
                  <div className={styles.empty}>ì¹œêµ¬ê°€ ì—†ìŠµë‹ˆë‹¤.</div>
              ) : (
                  <div className={styles.friendList}>
                    {friends.map((friend) => {
                      const isSelected = selectedFriends.includes(friend.userSignId);
                      return (
                          <div
                              key={friend.friendId}
                              onClick={() => toggleFriend(friend.userSignId)}
                              className={`${styles.friendItem} ${isSelected ? styles.selected : ''}`}
                          >
                            <div className={styles.friendInfo}>
                              <div className={styles.avatar}>
                                {friend.name?.[0] || friend.userSignId[0]}
                              </div>
                              <div>
                                <div className={styles.friendName}>{friend.name || friend.userSignId}</div>
                                <div className={styles.friendId}>@{friend.userSignId}</div>
                              </div>
                            </div>
                            {isSelected && <Check size={20} color="#2563eb" />}
                          </div>
                      );
                    })}
                  </div>
              )}
            </div>
          </div>

          <div className={styles.footer}>
            <button
                onClick={handleSubmit}
                disabled={selectedFriends.length === 0 || submitting}
                className={styles.submitBtn}
            >
              {submitting ? (
                  <>ìƒì„± ì¤‘...</>
              ) : (
                  <>ì±„íŒ… ì‹œì‘í•˜ê¸°</>
              )}
            </button>
          </div>
        </div>
      </>
  );
}